'use client';

import { useState } from 'react';
import { useRouter } from 'next/navigation';
import { styleText } from 'util';

const CreateContentPage = () => {
  const router = useRouter();

  const [title, setTitle] = useState('');
  const [tone, setTone] = useState('professional');
  const [length, setLength] = useState('medium');
  const [provider, setProvider] = useState('OPENAI');
  const [loading, setLoading] = useState(false);

  const getWordCount = () => {
    switch (length) {
      case 'short': return 300;
      case 'medium': return 500;
      case 'long': return 800;
      default: return 500;
    }
  };

  const getToneInstruction = () => {
    switch (tone) {
      case 'professional': return 'sur un ton professionnel';
      case 'casual': return 'sur un ton décontracté et accessible';
      case 'enthusiastic': return 'sur un ton enthousiaste et engageant';
      default: return '';
    }
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);

    const payload = {
      title,
      style: getToneInstruction(),
      length: getWordCount(),
      provider,
    };

    try {
      const res = await fetch(`/content`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        // body: JSON.stringify(payload),
        body: JSON.stringify({ title,styleText, length, provider }),
      });

      if (res.ok) {
        const data = await res.json();
        console.log('✅ Contenu généré :', data);
        router.push('/content');
      } else {
        console.error('❌ Erreur lors de la génération');
      }
    } catch (error) {
      console.error('❌ Erreur serveur', error);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="max-w-xl mx-auto p-4">
      <h1 className="text-2xl font-bold mb-6">Créer un contenu IA</h1>
      <form onSubmit={handleSubmit} className="space-y-4">

        <input
          type="text"
          placeholder="Titre du contenu"
          value={title}
          onChange={(e) => setTitle(e.target.value)}
          className="w-full p-2 border rounded"
          required
        />

        <select
          value={tone}
          onChange={(e) => setTone(e.target.value)}
          className="w-full p-2 border rounded"
        >
          <option value="professional">Professionnel</option>
          <option value="casual">Décontracté</option>
          <option value="enthusiastic">Enthousiaste</option>
        </select>

        <select
          value={length}
          onChange={(e) => setLength(e.target.value)}
          className="w-full p-2 border rounded"
        >
          <option value="short">Court (300 mots)</option>
          <option value="medium">Moyen (500 mots)</option>
          <option value="long">Long (800 mots)</option>
        </select>

        <select
          value={provider}
          onChange={(e) => setProvider(e.target.value)}
          className="w-full p-2 border rounded"
        >
          <option value="OPENAI">OpenAI</option>
          <option value="DEEPSEEK">DeepSeek</option>
          <option value="CLAUDEAI">Claude AI</option>
        </select>

        <button
          type="submit"
          disabled={loading}
          className="bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700"
        >
          {loading ? 'Génération en cours...' : 'Générer'}
        </button>
      </form>
    </div>
  );
};

export default CreateContentPage;
